@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
.details{
    padding: 30px 40px;
    background-color: #fff;
}

.Logo h1{
    color: #3751FE;
    font-size: 24px;
    font-weight: 700;
}

.Topic p{
    color: #3751FE;
    font-weight: 700;
    font-size: 32px;
    line-height: 50px;
    word-spacing: 10px;
    letter-spacing: 1px;
}

.digital-details .note{
    color: rgba(0, 0, 0, 0.60);
    font-size: 18px;
}

.log-button {
    padding: 20px 0;
}

.log-button button{
    width: 125px;
    height: 40px;
    outline: none;
    cursor: pointer;
}

.log-button .log{
    color: #fff;
    background-color: #3751FE;
    box-shadow: 0px 4px 3px rgba(0, 0, 0, 0.25);
    border: none;
    margin: 0 20px 0 0;
}

.log-button .sign{
    border: 1px solid #3751FE;
    color: #3751FE;
    background-color: #fff;
    margin: 0 0 0 20px;
}

.links{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0;
    margin: 20px 0 0;
    font-size: 14px;
}

.link a{
    text-decoration: none;
    color: #3751FE;
    cursor: pointer;
    font-weight: 700;
}




@media screen and (max-width:600px){
    .log-button button{
        width: 115px;
        height: 40px;
    }

    
    .Topic p{
        font-size: 26px;
        
        word-spacing: 0px;
        letter-spacing: 0px;
    }
}
.detailsForm form input{
    border: 1px solid #C1BBBB;
    outline: none;
    height: 50px;
    padding: 5px 0 5px 15px;
    width: 600px;
    font-size: 18px;
    color: #3751FE;
    
    /* position: relative; */
}

.detailsForm form label{
    color: rgba(0, 0, 0, 0.60);
    position: absolute;
    padding: 0 10px 10px;
    font-size: 14px;
    /* 
    padding: 10px 10px;
    pointer-events: none;
    transition: .3s ease-out; */
    
}

.email{
    margin: 0 0 20px;
}

.detailsForm form input:focus {
    /* transform: translateY(-20px); */
    border-left: 3px solid #3751FE;
}

.reset{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 0;
}

.reset label{
    color: #0000009C;
}


@media screen and (max-width:1350px){
    .detailsForm form input{
        height: 50px;
        width: 400px;
    }
}

@media screen and (max-width:1240px){
    .detailsForm form input{
        height: 50px;
        width: 400px;
        font-size: 16px;    
    }
}



@media screen and (max-width:950px){
    .detailsForm form input{
        height: 50px;
        width: 650px;
        font-size: 16px;    
    }
}

@media screen and (max-width:600px){
    .detailsForm form input{
        height: 50px;
        width: 300px;
        font-size: 16px;    
    }
}


*, *::before, *::after{
  box-sizing: border-box;
}

body{
  margin: 0px;
  padding: 0px;
  font-family: 'Roboto', sans-serif;
  background-color: #E5E5E569;
}

.app-details{
  float: left;
  width: 50%;
  height: 100vh;
  background-color: #FFF;
}

.app-clip{
  float: right;
  width: 50%;
  overflow: hidden;
}


@media screen and (max-width:950px){
  .app-details{
    float: none;
    width: 100%;
  }
  
  .app-clip{
    display: none;
  }
}
.clip-img{
    width: 100%;
    margin:150px 0 0;
}

.clip-img img{
    width: 100%;
}


